import { useState, useEffect } from 'react';
import { Box, Tabs, Tab, Paper, Button, useTheme, useMediaQuery, Divider, ButtonGroup } from '@mui/material';
import { IconCurrencyDollar as TransactionIcon } from '@tabler/icons';
import { useFormik } from 'formik';

import transactionSchema from 'schema/player.transaction.schema';

// Components
import MainCard from 'ui-component/cards/MainCard';
import DataTable from 'components/DataTable';
import NotFoundCard from 'components/NotFoundCard';
import Modal from 'components/ResponsiveModal';
import PlayerDeposit from './components/Forms/PlayerDepositForm';
import PlayerWithdraw from './components/Forms/PlayerWithdrawForm';

function Transaction() {
    const theme = useTheme();
    const isMobileDevice = useMediaQuery(theme.breakpoints.down('sm'));
    const [openDepositModal, setOpenDepositModal] = useState(false);
    const [openWithdrawModal, setOpenWithdrawModal] = useState(false);

    const { agentpermission, menupermission } = JSON.parse(localStorage.getItem('MAJESTIC_CASINO'));

    const agentPermissions = agentpermission.map((permission) => permission.AGENT_PERMISSION_KEY);
    const pageUrl = window.location.href.split('/')[window.location.href.split('/').length - 1];
    const menuPermissions = menupermission.filter(
        (permission) => permission.MENU_SLUG === `/${pageUrl.split('/')[pageUrl.split('/').length - 1]}s`
    )[0];

    const formik = useFormik({
        initialValues: { username_playerId: '', email: '', password: '', phone_no: '' },
        validationSchema: transactionSchema,
        onSubmit: (values) => {
            console.log(values);
        }
    });

    const columns = ['ID', 'username', 'name', 'email', 'phone_no', 'agent', 'status'];

    const options = {
        filter: false,
        print: false,
        download: false,
        selectableRows: false,
        rowsPerPage: 10,
        rowsPerPageOptions: [10, 20],
        jumpToPage: true,
        elevation: 2
    };

    const data = [];

    return (
        <Box>
            <Paper>
                <MainCard
                    title={!isMobileDevice && 'Player Transaction'}
                    secondary={
                        <ButtonGroup variant="contained" color="warning">
                            {agentPermissions.includes('ISDEPOSIT_PLAYER') && (
                                <Button
                                    startIcon={<TransactionIcon />}
                                    color="warning"
                                    onClick={() => setOpenDepositModal(!openDepositModal)}
                                    style={{ marginRight: 2 }}
                                >
                                    Deposit
                                </Button>
                            )}
                            {agentPermissions.includes('ISWITHDRAW_PLAYER') && (
                                <Button
                                    startIcon={<TransactionIcon />}
                                    color="warning"
                                    onClick={() => setOpenWithdrawModal(!openWithdrawModal)}
                                    style={{ marginLeft: 2 }}
                                >
                                    Withdraw
                                </Button>
                            )}
                        </ButtonGroup>
                    }
                >
                    {isMobileDevice && (
                        <>
                            <ButtonGroup variant="contained" color="warning" style={{ marginBottom: 15 }}>
                                {agentPermissions.includes('ISDEPOSIT_PLAYER') && (
                                    <Button
                                        startIcon={<TransactionIcon />}
                                        color="warning"
                                        onClick={() => setOpenDepositModal(!openDepositModal)}
                                        style={{ marginRight: 2 }}
                                    >
                                        Deposit
                                    </Button>
                                )}
                                {agentPermissions.includes('ISWITHDRAW_PLAYER') && (
                                    <Button
                                        startIcon={<TransactionIcon />}
                                        color="warning"
                                        onClick={() => setOpenWithdrawModal(!openWithdrawModal)}
                                        style={{ marginLeft: 2 }}
                                    >
                                        Withdraw
                                    </Button>
                                )}
                            </ButtonGroup>
                            <Divider />
                        </>
                    )}
                    <Box>
                        {data.length > 0 && menuPermissions.ISVIEW ? (
                            <DataTable title="Players Transaction List" data={data} columns={columns} options={options} />
                        ) : (
                            <NotFoundCard msg="Sorry, No data found" />
                        )}
                    </Box>
                </MainCard>
            </Paper>

            {/* DEPOSIT MODAL */}
            <Modal title="Player Deposit" open={openDepositModal} onClose={() => setOpenDepositModal(!openDepositModal)}>
                <PlayerDeposit formik={formik} openModal={openDepositModal} setOpenDepositModal={!openDepositModal} />
            </Modal>

            {/* WITHDRAW MODAL */}
            <Modal title="Player Withdraw" open={openWithdrawModal} onClose={() => setOpenWithdrawModal(!openWithdrawModal)}>
                <PlayerWithdraw
                    formik={formik}
                    openModal={openWithdrawModal}
                    setOpenWithdrawModal={() => setOpenWithdrawModal(!openWithdrawModal)}
                />
            </Modal>
        </Box>
    );
}

export default Transaction;
